<template lang="html">
  <div class="count-manage-page">
    <!-- 头部 -->
    <default-head header="账户管理" path="/personcenter"></default-head>

    <div class="count-box">
      <count-item class="borderB" textLeft="头像" textRight="" :image="userInfo.logo"
      :isAvatar="true" :isShow="true" @click.native="changePic"></count-item>
      <count-item class="borderB" textLeft="绑定银行卡" :textRight="bankTags[userInfo.isBank]" :warn="true"
        :isShow="true" @click.native="toBindCard"></count-item>
      <count-item class="borderB" textLeft="昵称" @click.native="updateNickname"
      :textRight="userInfo.nickName || '暂无'" :isShow="true"></count-item>
      <count-item class="borderB" textLeft="会员编号" :textRight="userInfo.vipNo || '暂无'"></count-item>
      <!-- <count-item class="borderB" textLeft="会员等级" textRight="普通会员" :warn="true" :isShow="true"></count-item> -->
      <count-item class="borderB" textLeft="账户安全" textRight="密码设置" :isShow="true" @click.native="$router.push('/accountsafe')"></count-item>
      <count-item class="borderB" textLeft="申请投诉" textRight="" :isShow="true" @click.native="$router.push('/complaint')"></count-item>
      <!-- <count-item class="borderB" textLeft="用户建议与反馈" textRight="建议反馈" :isShow="true"></count-item>
      <count-item class="borderB" textLeft="会员疑问" textRight="常见会员答疑" :isShow="true"></count-item> -->
      <input @change="changeFile" ref="changeImg" accept="image/*" type="file" style="display:none">
    </div>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import defaultHead from '@/components/head/defaultHead'
import countItem from '@/components/personcenter/countItem'
import minutils from '@/utils/minUtils'

export default {
  data () {
    return {
      userInfo: {
        isAuth: 0,
        isBank: 0,
        isPay: 0,
        logo: '',
        nickName: '',
        vipNo: 'hzl1234567'
      },
      bankTags: ['未设置', '已绑定', '已驳回']
    }
  },
  components: { defaultHead, countItem },
  computed: {
    userId () {
      return utils.userId()
    },
    userType () {
      return utils.getType()
    }
  },
  created () {
    this.ajaxGetAccountInfo({userId: this.userId})
  },
  methods: {
    updateNickname () {
      let self = this
      minutils.MessageBoxPrompt({
        title: '提示',
        options: {
          inputPlaceholder: '请输入用户昵称'
        },
        success (value, action) {
          if (!value) {
            minutils.Toast({ message: '请输入用户昵称', position: 'top' })
          } else {
            self.ajaxUpdateNickName({userId: self.userId, nickName: value})
          }
        }
      })
    },
    changePic () {
      var self = this
      minutils.MessageBoxConfirm({
        message: '您确认修改您的头像么？',
        title: '提示',
        options: { confirmButtonText: '确定', cancelButtonText: '取消' },
        success (action) {
          self.$refs.changeImg.click()
        }
      })
    },
    toBindCard () {
      if (this.userInfo.isAuth !== 3) {
        let path = this.userType === 0 ? '/personrecognize' : 'companyrecognize'
        let message = this.userType === 0 ? '您暂未个人真实性认证' : '您暂未企业真实性认证'
        this.isRecognize(path, message)
      } else {
        this.$router.push({path: 'bindbankcard', query: {isBank: this.userInfo.isBank}})
      }
    },
    isRecognize (path, message) {
      let self = this
      minutils.MessageBoxConfirm({
        message: message + ', 请认证后再绑定银行卡!',
        options: {confirmButtonText: '立即认证', cancelButtonText: '取消认证'},
        success (action) {
          self.$router.push(path)
        }
      })
    },
    changeFile (e) {
      utils.uploadPic(e.target.files, (data) => {
        this.ajaxUploadPic({fileName: data[0].fileName, fileBase64: data[0].base64Img})
      })
    },
    ajaxUpdateNickName (data) {
      api.updateNickName(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          this.userInfo.nickName = result.data
          minutils.MessageBox({message: '修改个人昵称成功'})
        }
      })
    },
    ajaxUploadPic (data) {
      api.uploadPic(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          this.ajaxUpdateHeadImg({userId: this.userId, headImg: result.data})
        }
      })
    },
    ajaxUpdateHeadImg (data) {
      api.updateHeadImg(data).then(response => {
        let result = response.body
        if (result.isSuccess && result.data) {
          this.userInfo.logo = result.data
          minutils.MessageBox({message: '更换个人头像成功'})
        }
      })
    },
    ajaxGetAccountInfo (data) {
      api.getAccountInfo(data).then(response => {
        let result = response.body
        if (result.isSuccess && result.data) {
          Object.keys(this.userInfo).forEach(k => {
            if (result.data[k]) {
              this.userInfo[k] = result.data[k]
            }
          })
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.count-manage-page {
  padding-top: .88rem;
  .count-box {
    background: @whiteColor;
  }
}

</style>



